.restaurant-detail {
    font-family: Arial, sans-serif;
    padding: 20px;
}

.top-section {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
}

.image-box,
.map-box {
    min-height: 300px;
    flex: 1;
    border: 1px solid #ffa726;
    padding: 20px;
    text-align: center;
    background-color: #fff;
}

.main-section {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
}

.details {
    width: 40%;
    flex: 1;
    border: 1px solid #ffa726;
    padding: 20px;
    background-color: #fff;
}

.details h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.rating {
    color: orange;
}

.actions {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
    font-size: 14px;
}

.details p {
    margin: 5px 0;
}

.reviews-section {
    width: 40%;
    flex: 1;
    border: 1px solid #ffa726;
    padding: 20px;
    background-color: #fff;
}

.reviews-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.review-button {
    background-color: #ffa726;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 5px;
}

.review-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.review-item {
    border: 1px solid #ffa726;
    padding: 10px;
    flex: 1;
    max-width: 300px;
    background-color: #fff;
}

.review-item .rating {
    color: orange;
}

.review-image {
    border: 1px solid #ddd;
    background-color: #f9f9f9;
    height: 100px;
    margin: 10px 0;
    text-align: center;
    line-height: 100px;
}

.review-actions {
    font-size: 14px;
    color: #555;
}

.more-reviews {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #ffa726;
    border: none;
    color: white;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .main-section {
        flex-direction: column;
    }

    .details,
    .reviews-section {
        flex: 1;
        margin-bottom: 20px;
    }
}